@charset "UTF-8";

@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);


// Some vars
$animation-duration: 5.4s;

$size: 15px;
$margin: 5px;
$color: #083c5d;
$next: $size + $margin;

$loader-size: ($size * 3) + ($margin * 2);

$background-color:  #1fbeca;

$header-size: 60px;

body{
    font-family: 'Roboto',sans-serif;
    font-weight: 300;
    background: #D9D9D9;
    position:relative;
    width: 100%;
    max-width:100%;
    overflow-x: hidden;
    #app{
        padding: 0;
        width: 100%;
        overflow-x: hidden;
    }
    .fixed_header{
        position: fixed;
        top: 0;
        left: 0;
        height:500px;
        width: 60px;
        background: #083C5D;
        z-index:5;
        color: #fff;
        line-height: 35px;
    }
}



h1.greet-message{
  text-align: center;
  font-weight: 300;
}
ul.list-group{
  list-style: none;
  margin:20px auto;
  padding:0;
  text-align: center;
  li{
    display: inline-block;
    padding: 10px;
    background: #FFEB3B;
    margin: 0 5px;
    transform: skewX(7deg) skewY(2deg);
  }
}
.fork-me{
  position: fixed;

  top: 0;
  right: 0;
  border: 0;
  max-width: 50%;
  height: 90px;
  display: none;
  z-index: 10;
  cursor: pointer;
  img{
    max-width: 100%;
    max-height: 100%;
    cursor:pointer;
  }

}

.pixel_color_input{
    border: 5px solid #000;
}
.pixel_tools_menu{
    position:relative;
    display: block;
    margin:0;
    padding:0;
    .pixel_tool{
        padding: 10px;
        box-sizing: border-box;
        display: inline-block;
        height: $header-size;
        vertical-align: middle;
        b {
            font-weight: 100;
            font-size: 10px;
            display: block;
            line-height: 1.5;
            padding: 5px;
        }
        &.file_uploader{
            .tool-item{
                .child-reveal{
                    padding:10px 10px 20px;
                }
            }
        }
        &.size-picker-wrap{width:100px;}
         .page-link.icon_item{
             padding-top:0; padding-bottom:0;
             padding-left: .5rem; padding-right: .5rem;
             svg{
                max-height: 36px;
                margin: 0;
                display: block;
             }
         }
         .tool-item{
             position:relative;
             &.active{
                 .btn.tool-button{
                     background-color: #5bc0de;
                 }
             }
             .child-reveal{
                 position: absolute;
                 top: -10px;
                 padding: 0 20px 20px;
                 background: #083c5c;
                 left: 100%;
                 width: 200px;
                 box-sizing: border-box;
             }
             .btn.tool-button{
                 color: #353535;
                 background-color: #f1f3f5;
                 svg{
                     width: 20px;
                     height: 30px;
                     fill: #353535;
                 }
             }
         }

        .btn{
            display: block;
            height: 100%;
            padding:0 .5rem;
        }
        svg{
            width: 20px;
            height: 30px;
            fill: #fff;
        }

        .acedzn-color-picker{
            width: 100%;
            height: 100%;
            position: relative;
            display: block;

            .color_swatch{
                width: 40px; display:inline-block; height:100%; vertical-align:top;
                box-shadow: inset 0 0 0px 2px #fff;

                svg{height:40px;}
            }
            svg{
                width: 20px;
                padding: 1px;
                display: block;
                margin: 0 auto;
            }
            .twitter-picker{

                display: none!important;
                width: 280px!important;
                position: absolute!important;
                bottom: -55px!important;
                z-index: 13!important;
                &.open{
                    display: flex!important;
                    z-index: 13!important;
                }
            }
            span{
                display: inline-block;
                padding:2px;
                vertical-align:top;
                width:20%;
                color: rgba(255, 255, 255, .7);
                &.eraser{
                    padding:0; margin-left:5px; width:55px;
                    div{
                        background-image:url('../images/transparent_icon_bg.jpg');
                        border-radius:0;
                        svg{fill: #ec134c;}
                    }
                }
                &.active{
                    div,div:hover{box-shadow: inset 0 0 0 1px #083c5c, 0 0 10px -2px #fff}
                }
                div{
                    cursor: pointer;
                    font-size:8px;
                    width:100%; height:40px;line-height:40px;
                    text-align: center;
                    border-radius: 5px;
                    background-repeat: no-repeat;
                    background-position: center center;
                    background-size: cover;
                    &:hover{box-shadow: inset 0 0 10px 0 rgba(255, 255, 255, .5), 0 0 10px -2px #fff; cursor:pointer;}
                    svg{fill: #ec134c;}
                }
            }
            .picker-popup{
                display: none;
                width: 320px;
                position: absolute;
                z-index: 10;
                top: -30px;
                left: 50px;
                background: #083c5c;
                border-radius: 0 5px 5px 0;
                padding: 5px;
                &.open{
                    display: block;
                }

                hr{
                    margin: 5px;
                    border-radius: 50%;
                    border-color: #135b86;
                }
                b{
                    font-weight: 100;
                    font-size: 10px;
                    display: block;
                    line-height: 1.5;
                    padding: 5px;
                }
            }
        }

        .rangeslider.rangeslider-horizontal{
            margin: 10px 0 0; height:5px;
            .rangeslider__handle{
                width: 30px; height: 15px;
                &:after{
                    width: 20px; height: 9px;
                    top: 2px; left: 4px;
                    border-radius: 10px;
                }
            }
        }
        .rangeslider-horizontal .rangeslider__handle-tooltip{top:37px!important;}
        .rangeslider-horizontal .rangeslider__handle-tooltip:after {
            border-left: 8px solid transparent;
            border-right: 8px solid transparent;
            border-bottom: 8px solid rgba(0,0,0,.8)!important;
            border-top: 0px solid transparent!important;
            left: 50%;
            top: -8px!important;
            bottom:auto!important;
        }

    }
}

.file_uploader{
  label{
    cursor:pointer;
    display: block; width: 100%; height: 38px;
    background: #3498db;
    box-shadow: inset 0 0 0 5px #FFFFFF;
    color:#fff; font-weight:bold; letter-spacing:2px;
    padding: 0 20px; line-height: 38px; position:relative;
    &:after{
      content:'+'; font-size:40px; color:#3498db;
      text-align:center; line-height: 33px;
      position:absolute; right:0; top:0;
      width:20%; max-width:100px;  height:100%;
      background-color: #FFFFFF;

    }
    &:hover{
      box-shadow: inset 0 0 0 5px #45a7e8;
      &:after{
        color:#ffffff;
        background-color: #45a7e8;
      }
    }
  }
  input{
    position: absolute; top: 0; left: 0;
    visibility: hidden; opacity: 0; width:100%;
  }
}
.preview-block-wrap{
    display:none;
    &.active{display: block;}
    position: fixed; top: 0; left: 0; z-index: 5;
    width: 100%;height: 100%;
    background: transparent;
}
.preview-block-overlay{
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.53);
    left: 0;
    z-index: 5;
    padding: 50px;
    @media only screen and (min-width: 600px){
        padding: 100px;
    }
}
.preview-block{
    position: relative;
    height: 100%;
    min-height: 300px;
    background: #fff;
    box-sizing: border-box;
    display: block;
    padding: 36px;
    margin: 50px;
    z-index: 6;
    img, svg{
        max-width: 100%;
    }
    .code-block{
        position: relative;
        width: 550px;
        max-width:100%;
        height: 250px;
        textarea{width:100%; height:100%;}
    }
}


textarea{
    width: 100%;
    min-height: 200px;
}

.canvas_wrap{
    position: relative;
    canvas.grid_canvas{
        position:absolute;
        top:0;
        left:0;
        z-index:0;
    }
    canvas.pixel_canvas{
        position:relative;
        top:0;
        left:0;
        z-index:2;
    }
}
.container{
    position:inherit;
}

@media only screen and (min-width: 600px){
    .fork-me{
        display:block;
    }
}






