// Loader
.loader_wrap{
    position: absolute;
    top:0;left: 0;
    display: block;
    z-index: 4;
    background:rgba(0,0,0,.3);

    &>div{
        position:absolute;top:50%;left:50%;transform: translate(-50%,-50%);
    }

}

.progress_loader{
  position: relative;
  width: $loader-size;
  height: $loader-size;
  //background: url('../images/blocks-loader.gif');
  //background-size: cover;
  z-index: 5;
  //box-shadow: 0 0 0 $color;

  &::before {
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: $size;
    height: $size;
    content: '';
    box-shadow:
      $next 0 0 rgba(255,255,255,0),
      ($next * 2) 0 0 rgba(255,255,255,0),
      0 (0 - $next) 0 rgba(255,255,255,0),
      $next (0 - $next) 0 rgba(255,255,255,0),
      ($next * 2) (0 - $next) 0 rgba(255,255,255,0),
      0 (0 - ($next * 2)) rgba(255,255,255,0),
      $next (0 - ($next * 2)) rgba(255,255,255,0),
      ($next * 2) (0 - ($next * 2)) rgba(242,205,123,0);
    animation: loader $animation-duration linear forwards infinite;
  }

  &::after {
    position: absolute;
    bottom: 10px;
    left: 0;
    display: block;
    width: $size;
    height: $size;
    background-color: $color;
    opacity: 0;
    content: '';
    animation: base $animation-duration linear forwards infinite;
  }

}

// Animation
@keyframes base {
  0% {
    bottom: 10px;
    opacity: 0;
  }

  5%, 50% {
    bottom: 0;
    opacity: 1;
  }
  55%, 100% {
    bottom: -10px;
    opacity: 0;
  }
}

